import { TableRow as _TableRow } from "@mui/material";
import { styled } from "@mui/material/styles";

const HorarioTableRow = styled(_TableRow)(({ theme }) => ({
	height: "100%",
	"&:nth-of-type(odd)": {
		backgroundColor: "#000",
	},
	"&:last-child td, &:last-child th": {
		border: 0,
	},
	"&:nth-child(9)": {
		borderTop: `20px solid #777`,
	},
}));

const HorarioTableHeadRow = styled(_TableRow)(({ theme }) => ({
	backgroundColor: "#000",
	height: 50,
}));

export { HorarioTableRow, HorarioTableHeadRow };
