import HORARIO_ASIGNATURAS from "static/horario_asignaturas.json";

import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button, Autocomplete, TextField, Box } from "@mui/material";
import * as React from "react";
import { SettingsContext } from "contexts/SettingsContext";

export default function SedeJornadaPrompt() {
	const [open, setOpen] = React.useState(true);
	const [sede, setSede] = React.useState("");
	const [jornada, setJornada] = React.useState("");
	const [_, setSettings] = React.useContext(SettingsContext);
	const handleClose = () => setOpen(false);

	//@ts-ignore
	const sedeOptions = React.useMemo(() => Object.keys(HORARIO_ASIGNATURAS), []);
	//@ts-ignore
	const jornadaOptions = React.useMemo(() => (sede ? Object.keys(HORARIO_ASIGNATURAS[sede]) : []), [sede]);

	return (
		<Dialog
			open={open}
			fullWidth
			PaperProps={{
				component: "form",
				onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
					event.preventDefault();
					const formData = new FormData(event.currentTarget);
					const formJson = Object.fromEntries((formData as any).entries());

					let { sede, jornada } = formJson;

					localStorage.setItem("sede", sede);
					localStorage.setItem("jornada", jornada);
					setSettings!({ sede, jornada });

					handleClose();
				},
			}}
		>
			<DialogTitle>Específica sede y jornada dentro de la USM</DialogTitle>

			<Box margin={0} display="flex" flexDirection="row">
				<DialogContent>
					<Autocomplete
						options={sedeOptions}
						fullWidth
						value={sede}
						onChange={(_, v) => v && setSede(v)}
						disableClearable
						autoComplete
						noOptionsText="No hay sedes disponibles"
						renderInput={params => (
							<TextField
								{...params}
								required
								margin="dense" //
								id="sede"
								name="sede"
								label="Sede"
								type="text"
								fullWidth
								variant="standard"
							/>
						)}
					/>
				</DialogContent>
				<DialogContent>
					<Autocomplete
						options={jornadaOptions}
						fullWidth
						value={jornada}
						disabled={!sede}
						onChange={(_, v) => v && setJornada(v)}
						disableClearable
						autoComplete
						noOptionsText="No hay jornadas disponibles"
						renderInput={params => (
							<TextField
								{...params}
								required
								margin="dense" //
								id="jornada"
								name="jornada"
								label="Jornada"
								type="text"
								fullWidth
								variant="standard"
							/>
						)}
					/>
				</DialogContent>
			</Box>
			<DialogActions>
				<Button disabled={!jornada || !sede} variant="contained" type="submit">
					Listo
				</Button>
			</DialogActions>
		</Dialog>
	);
}
