import { Dispatch, useEffect, useMemo } from "react";
import { Box, Chip, Paper } from "@mui/material";
import { MeetingRoom } from "@mui/icons-material";
import { Ramo, Bloque } from "../../typings/horario";
import { getRamoColor, setRamoFlag } from "../../utils/RamoUtils";
import * as Time from "../../utils/Time";
import Color from "color";
import { TIPO_BLOQUE_TO_EMOJI, TIPO_BLOQUE_TO_FULL } from "constants/Globals";
import zIndex from "@mui/material/styles/zIndex";

export default function BloqueCell(props: { bloque: Bloque; ramo: Ramo; selectedRamos: Ramo[]; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const { bloque, ramo, selectedRamos, setSelectedRamos } = props;

	const isGhost = ramo.ghost;
	const prevContinuations = useMemo(() => {
		let continuations = [];
		let b = bloque.bloque;
		for (const r of ramo.horario.filter(b => b.dia === bloque.dia && b.bloque < bloque.bloque).reverse()) {
			if (r.bloque === b - 1) continuations.push(r);
			else continue;
			b = r.bloque;
		}
		return continuations;
	}, [bloque, ramo]);
	const nextContinuations = useMemo(() => {
		let continuations = [];
		let b = bloque.bloque;
		for (const r of ramo.horario.filter(b => b.dia === bloque.dia && b.bloque > bloque.bloque)) {
			if (r.bloque === b + 1) continuations.push(r);
			else continue;
			b = r.bloque;
		}
		return continuations;
	}, [bloque, ramo]);

	const colorRamo = useMemo(() => {
		let col = Color(ramo.color ?? getRamoColor(ramo.sigla));
		switch (bloque.tipo) {
			case "Otr":
			case "Ayu":
				col = col.desaturate(0.7).mix(Color("#fff"), 0.25);
				break;
			case "Lab":
				col = col.mix(Color("#fff"), 0.25);
				break;
			case "Prá":
				col = col.mix(Color("#f00"), 0.1);
				break;
			case "Rec":
				col = col.mix(Color("#00f"), 0.1);
				break;
		}

		if (isGhost) col = col.mix(Color("#000"), 0.6);
		return col.hex();
	}, [ramo, bloque, selectedRamos]);
	const collisions = selectedRamos
		.map(r => r.horario)
		.flat()
		.filter(b => b.bloque === bloque.bloque && b.dia === bloque.dia).length;

	const isTop = !prevContinuations.length && nextContinuations.length;
	const isMiddle = nextContinuations.length && prevContinuations.length;
	const isBottom = !nextContinuations.length && prevContinuations.length;

	return (
		<>
			<Box
				onMouseEnter={_ => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", true))}
				onMouseLeave={_ => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", false))}
				zIndex={20 - bloque.bloque}
				style={{
					backgroundColor: Color(colorRamo).darken(0.3).rotate(-2).hex(),
				}}
				className={["Horario-bloque", isMiddle ? "middle" : isBottom ? "bottom" : "top"].join(" ")}
				key={ramo.paralelo}
			>
				<Box
					position="relative"
					display="flex"
					width="100%"
					overflow={prevContinuations.length % 2 === 0 && collisions < 3 ? "visible" : "hidden"}
					flexDirection="row"
					alignItems="center"
					justifyContent="center"
					gap="5px"
					height="100%"
					style={{
						position: "relative",
						fontSize: "1.2vmin",
						opacity: !isGhost ? 1 : 0.4,
						lineHeight: "1.2vmin",
					}}
				>
					<Box width="100%">
						<Box
							margin={0}
							width="50%"
							style={{
								position: "relative",
								transformOrigin: "left top",
								transform: `scale(${collisions <= 1 ? "2" : collisions > 2 ? "0.6" : "1"})`,
								minHeight: "40px",
								translate: [
									//
									"10%",
									!isBottom ? "25%" : "40%",
								].join(" "),
							}}
						>
							{!prevContinuations?.[0] || prevContinuations[0].sala !== bloque.sala ? (
								<>
									<span title={ramo.nombre}>
										<b>{ramo.sigla.slice(0, collisions > 2 ? 3 : undefined)}</b>
									</span>{" "}
									<br />
									<span style={{ color: "#000", opacity: 0.8 }}>
										<b>{ramo.paralelo}</b>{" "}
										<span style={{ filter: "grayscale(1) contrast(3)" }} title={TIPO_BLOQUE_TO_FULL[bloque.tipo]}>
											{TIPO_BLOQUE_TO_EMOJI[bloque.tipo]}
										</span>
									</span>
								</>
							) : prevContinuations[0].tipo !== bloque.tipo ? (
								<span style={{ color: "#000", opacity: 0.6, fontWeight: "bolder" }}>{TIPO_BLOQUE_TO_FULL[bloque.tipo].toUpperCase()}</span>
							) : (
								<></>
							)}
						</Box>
						{!prevContinuations?.[0] || prevContinuations[0].sala !== bloque.sala ? (
							<Chip //
								icon={<MeetingRoom fontSize="small" style={{ opacity: 0.5, color: "#fff", height: "80%" }} />}
								label={
									<i>
										{bloque.sala //
											.replace("LAB. DE TECNOLOGIA DE ALTA TENSION", "LAB-A-TENSIÓN")
											.split(" ")[0]
											.replace("SIN", "SIN SALA")}
									</i>
								}
								title={bloque.sala}
								style={{
									position: "absolute",
									height: "min-contents",
									right: "5%",
									bottom: "70%",
									backgroundColor: "#000",
									transformOrigin: "right",
									transform: `scale(${collisions <= 1 ? "1.2" : "0.9"}) translateY(100%)`,
									fontSize: "1.1vmin",
								}}
							/>
						) : (
							<></>
						)}
					</Box>
				</Box>
			</Box>
		</>
	);
}
