import * as React from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import { Autocomplete, Box, Chip, DialogContentText, Paper, Typography } from "@mui/material";
import { AccountCircle, People } from "@mui/icons-material";
import { DIAS } from "../../constants/Globals";
import { RamosContext } from "contexts/RamosContext";
import { Ramo } from "typings/horario";

export default function BloquePrompt(props: { open: boolean; selectedRamos: Ramo[]; openSetter: React.Dispatch<React.SetStateAction<boolean>>; onAddRamo: (sigla: string, paralelo: string) => any }) {
	const { open, openSetter, onAddRamo, selectedRamos } = props;
	const [sigla, setSigla] = React.useState("");
	const [paralelo, setParalelo] = React.useState("");
	const [ramos] = React.useContext(RamosContext);

	const handleClose = () => void openSetter(false) || setSigla("");
	const ramoOptions = React.useMemo(() => [...new Set(ramos!.map(r => `${r.sigla} ${r.nombre}`.normalize("NFD").replace(/[\u0300-\u036f]/g, "")))], [ramos]);
	const paraleloOptions = React.useMemo(() => ramos!.filter(r => r.sigla === sigla).map(r => `${r.paralelo} ${r.profesor}`), [sigla]);
	const selectedRamo = React.useMemo(() => (sigla && paralelo ? ramos!.find(r => r.sigla === sigla && r.paralelo === paralelo) : null), [sigla, paralelo]);
	const ramoAlready = React.useMemo(() => selectedRamos.some(r => r.sigla === sigla), [sigla, selectedRamo]);

	React.useEffect(() => setParalelo(paraleloOptions.length === 1 && !ramoAlready ? paraleloOptions[0].split(" ")[0] : ""), [paraleloOptions]);

	return (
		<Dialog open={open} onClose={handleClose} fullWidth maxWidth="md">
			<DialogTitle>Añadir ramo</DialogTitle>
			{selectedRamo ? (
				<DialogContent>
					<DialogContentText>
						{selectedRamo.nombre}
						<br />
						<Box display="flex" marginTop={1} gap={1}>
							<Chip icon={<AccountCircle />} label={selectedRamo.profesor} />
							<Chip icon={<People />} label={`${selectedRamo.cupo} POR PARALELO`} />
						</Box>
						<Box display="flex" margin={1} gap={1}>
							{selectedRamo.horario.map(b => {
								const isOnTope = selectedRamos
									.map(r => r.horario)
									.flat()
									.some(_ => _.bloque === b.bloque && _.dia === b.dia);
								return (
									<Paper //
										title={`${b.tipo === "Ayu" ? "Ayudantía" : "Cátedra"}${isOnTope ? " con TOPE" : ""} a las ${b.hora} en ${b.sala} (${b.campus}).`}
										className={["Horario-dialog-bloque", b.tipo === "Ayu" ? "ayudantia" : "", isOnTope ? "tope" : ""].join(" ")}
									>
										<Typography variant="body2">
											<span className="Horario-dialog-bloque_number">{b.bloque}</span>
											<span className="Horario-dialog-bloque_day">{DIAS[b.dia]}</span>
											<span className="Horario-dialog-bloque_sala">{b.sala}</span>
										</Typography>
									</Paper>
								);
							})}
						</Box>
					</DialogContentText>
				</DialogContent>
			) : (
				<></>
			)}
			<Box margin={0} display="flex" flexDirection="row">
				<DialogContent autoFocus style={{ flex: 2 }}>
					<Autocomplete
						options={ramoOptions}
						fullWidth
						value={sigla}
						autoFocus
						onChange={(_, v) => v && void setSigla(curr => v.split(" ")[0] ?? curr)}
						disableClearable
						autoComplete
						filterSelectedOptions
						noOptionsText="No encontré ramos :("
						filterOptions={(options, state) =>
							options.filter(op =>
								op.includes(
									state.inputValue
										.toUpperCase()
										.normalize("NFD")
										.replace(/[\u0300-\u036f]/g, "")
								)
							)
						}
						renderOption={(
							props,
							option: string,
							state //
						) => (
							<li {...props} style={{ display: "flex", gap: "10%" }}>
								{(option => {
									const splitted = option.split(" ");
									const sigla = splitted.shift();
									const nombre = splitted.join(" ");
									return (
										<>
											<b>{sigla}</b> <i style={{ opacity: 0.6 }}>{nombre}</i>
										</>
									);
								})(option)}
							</li>
						)}
						renderInput={params => <TextField {...params} required margin="dense" label="Sigla/Nombre" type="text" fullWidth autoFocus variant="standard" inputProps={{ ...params.inputProps, style: { textTransform: "uppercase" } }} />}
					/>
				</DialogContent>
				<DialogContent style={{ flex: 1 }}>
					<Autocomplete
						options={paraleloOptions}
						fullWidth
						disabled={!sigla || paraleloOptions.length <= 1 || ramoAlready}
						value={paraleloOptions.length === 1 ? paraleloOptions[0] : paralelo}
						onChange={(_, v) => v && setParalelo(v.split(" ")[0])}
						disableClearable
						filterSelectedOptions
						autoComplete
						noOptionsText="No hay paralelos"
						filterOptions={(options, state) => options.filter(op => op.toLowerCase().includes(state.inputValue.toLowerCase()))}
						renderOption={(
							props,
							option: string,
							state //
						) => (
							<li {...props} style={{ display: "flex", gap: "10%" }}>
								{(option => {
									const splitted = option.split(" ");
									const paralelo = splitted.shift();
									const profesores = splitted.join(" ");
									const bloques = ramos?.find(r => r.sigla === sigla && r.paralelo === paralelo)?.horario;

									return (
										<>
											{bloques?.length &&
											selectedRamos
												.map(r => r.horario)
												.flat()
												.some(_ => bloques.find(b => b.dia === _.dia && b.bloque === _.bloque))
												? "⚠️ "
												: ""}
											<b>{paralelo}</b> <i style={{ opacity: 0.6 }}>{profesores}</i>
										</>
									);
								})(option)}
							</li>
						)}
						renderInput={params => (
							<TextField
								{...params}
								required
								error={ramoAlready}
								helperText={ramoAlready ? "Ramo ya añadadido." : ""}
								margin="dense" //
								label="Paralelo"
								type="text"
								fullWidth
								variant="standard"
							/>
						)}
					/>
				</DialogContent>
			</Box>
			<DialogActions>
				<Button onClick={handleClose}>Cancelar</Button>
				<Button disabled={!(paralelo && sigla)} variant="contained" onClick={_ => void onAddRamo(sigla, paralelo) || handleClose()}>
					Confirmar
				</Button>
			</DialogActions>
		</Dialog>
	);
}
