import * as React from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

import "./App.css";
import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";

import Horario from "./pages/Horario";
import { SettingsContext, Settings } from "contexts/SettingsContext";
import { RamosContext } from "contexts/RamosContext";
import { Ramo } from "typings/horario";

const darkTheme = createTheme({
	palette: {
		mode: "dark",
	},
});

function App() {
	const [settings, setSettings] = React.useState<Settings>({
		sede: localStorage.getItem("sede") ?? "",
		jornada: localStorage.getItem("jornada") ?? "",
	});
	const [ramos, setRamos] = React.useState<Ramo[]>([]);

	return (
		<ThemeProvider theme={darkTheme}>
			<CssBaseline />
			<SettingsContext.Provider value={[settings, setSettings]}>
				<RamosContext.Provider value={[ramos, setRamos]}>
					<Horario />
				</RamosContext.Provider>
			</SettingsContext.Provider>
		</ThemeProvider>
	);
}

export default App;
