import { Ramo } from "../typings/horario";

const SIGLA_COLORS: { [sigla: string]: string } = {
	MAT: "#00838f",
	FIS: "#00838f",
	IWI: "#2e58a7",
	INF: "#2e58a7",
	IWN: "#6ba8d1",
	ICS: "#6ba8d1",
	IWG: "#c54b73",
	DEW: "#90aa3a",

	// INF
	INF239: "#fdde15",
	INF236: "#fdde15",
	INF225: "#fdde15",
	INF322: "#fdde15",
	INF260: "#991b1e",
	INF270: "#991b1e",
	INF292: "#991b1e",
	INF293: "#991b1e",
	INF266: "#991b1e",
	INF360: "#991b1e",
	INF245: "#6d57a5",
	INF280: "#00ad5d",
};

const getRamoColor = (sigla: string) => SIGLA_COLORS[sigla] ?? SIGLA_COLORS[sigla.slice(0, 3)] ?? "#999";
const setRamoFlag = (ramos: Ramo[], sigla: string | undefined, flag: keyof Ramo, value: any) => [
	...ramos.map(r => {
		let equals = r.sigla === sigla?.replace("!", "");
		if (sigla?.startsWith("!")) equals = !equals;

		if (sigla === undefined || equals)
			if (!value) delete r[flag];
			//@ts-ignore
			else r[flag] = value;
		return r;
	}),
];

export { getRamoColor, setRamoFlag };
