import React, { useEffect } from "react";

import { AccountCircle, Add, Delete, FileUpload, FormatColorReset, MoreVert, People, Save, Visibility, VisibilityOff } from "@mui/icons-material";
import { Ramo } from "../../typings/horario";
import { getRamoColor, setRamoFlag } from "../../utils/RamoUtils";
import { useRef, Dispatch, useMemo, useState } from "react";
import { Box, Button, ButtonGroup, Chip, Divider, FormControl, IconButton, InputLabel, Link, Menu, MenuItem, Select, Typography } from "@mui/material";
import { saveAs } from "file-saver";
import { MuiColorInput } from "mui-color-input";
import BloquePrompt from "../components/BloquePrompt";
import Color from "color";

import * as Random from "../../utils/Random";
import { SettingsContext } from "contexts/SettingsContext";
import { RamosContext } from "contexts/RamosContext";

export default function ActionButtons(props: { selectedRamos: Ramo[]; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const uploadInput = useRef<HTMLInputElement>(null);
	const [bloqueDialogOpen, setBloqueDialogOpen] = useState(false);
	const [ramos] = React.useContext(RamosContext);
	const [settings, setSettings] = React.useContext(SettingsContext);

	useEffect(() => {
		//@ts-ignore
		window.PayPal.Donation.Button({
			env: "production",
			hosted_button_id: "KPMT2CNTRPY24",
			image: {
				src: "https://www.paypalobjects.com/en_US/i/btn/btn_donate_SM.gif",
				alt: "Donate with PayPal button",
				title: "PayPal - The safer, easier way to pay online!",
			},
		}).render("#donate-button");
	}, []);

	return (
		<>
			<Box margin="10px" gap="10px" display="flex" position="relative" alignItems="center">
				<Button variant="contained" startIcon={<Add />} onClick={_ => setBloqueDialogOpen(true)}>
					Añadir
				</Button>
				<ButtonGroup variant="contained">
					<Button
						variant="outlined"
						title="Guardar horario"
						onClick={() => {
							if (!props.selectedRamos) return;

							const chars = JSON.stringify(props.selectedRamos.map(r => [r.sigla, r.paralelo, r.color, r.hidden]));
							let bytes = new Array(chars.length);
							for (var i = 0; i < chars.length; i++) bytes[i] = chars.charCodeAt(i);

							saveAs(new Blob([new Uint8Array(bytes)]), "horario.husm");
						}}
					>
						<Save />
					</Button>
					<Button variant="outlined" title="Cargar horario" onClick={() => uploadInput.current?.click()}>
						<input
							ref={uploadInput}
							hidden
							multiple={false}
							accept=".husm"
							type="file"
							onChange={async ev => {
								ev.stopPropagation();
								ev.preventDefault();

								if (!ramos) return alert("Por favor, específica SEDE y JORNADA para poder importar.");

								const file = ev.target.files?.[0];
								if (!file) return;
								uploadInput.current!.value = "";

								const json = (await file.text()) ?? [];
								const horario = JSON.parse(json);
								if (!horario) return alert("El horario cargado está vacío.");

								props.setSelectedRamos(
									horario.map((r: [string, string, string, string]) => ({
										...ramos!.find((ramo: Ramo) => ramo.sigla === r[0] && ramo.paralelo === r[1]), //
										...(r[2] ? { color: r[2] } : {}),
										...(r[3] ? { hidden: r[3] } : {}),
									}))
								);
							}}
						/>
						<FileUpload />
					</Button>
				</ButtonGroup>
				<IconButton color="error" onClick={() => window.confirm("Limpiar horario actual?") && props.setSelectedRamos([])}>
					<Delete />
				</IconButton>
				<Box position="absolute" display="flex" right="0" height="calc(100% + 10px * 2)" margin="-10px" alignItems="center">
					<Typography variant="h6" margin="0 20px" alignContent="center" lineHeight={1}>
						Ramos asignados
						<br />
						{settings?.sede && settings?.jornada ? (
							<Link component="button" onClick={() => setSettings!({ sede: "", jornada: "" })} variant="body1">
								{settings?.sede} — {settings?.jornada}
							</Link>
						) : (
							<></>
						)}
					</Typography>
					<Box alignContent="center" maxHeight="70%" margin="0 10px" gap="10px" display="flex" position="relative" style={{ opacity: props.selectedRamos.length ? 1 : 0 }}>
						{props.selectedRamos
							.sort((a, b) => Random.decimalHash(getRamoColor(a.sigla)) - Random.decimalHash(getRamoColor(b.sigla)))
							?.map((ramo, i) => (
								<RamoButton key={i} setSelectedRamos={props.setSelectedRamos} ramo={ramo} />
							))}
					</Box>
				</Box>
				{/* <Typography variant="h6" margin="0 20px" alignContent="center">
					Hecho por Lukka
				</Typography>
				<div id="donate-button"></div> */}
			</Box>
			<BloquePrompt
				open={bloqueDialogOpen}
				openSetter={setBloqueDialogOpen}
				selectedRamos={props.selectedRamos}
				onAddRamo={(sigla, paralelo) => {
					const ramo = ramos!.find(r => r.sigla === sigla && r.paralelo === paralelo);
					if (!ramo) return alert(`Ramo "${sigla}" con paralelo ${paralelo} no encontrado!`);
					let ocurrencia: Ramo | undefined;
					if ((ocurrencia = props.selectedRamos.find(r => r.sigla === ramo.sigla))) return alert(`Ramo "${sigla}" ya registrado con paralelo ${ocurrencia.paralelo}!`);
					props.setSelectedRamos(curr => [...curr, ramo]);
				}}
			/>
		</>
	);
}

function RamoButton(props: { ramo: Ramo; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const { ramo, setSelectedRamos } = props;
	const [settings] = React.useContext(SettingsContext);
	const [ramos] = React.useContext(RamosContext);

	const ramosParalelos = useMemo(() => ramos!.filter(r => r.sigla === ramo.sigla), [ramo, ramos]);
	const [bloqueMenuAnchor, setBloqueMenuAnchor] = useState<null | HTMLElement>(null);

	const ramoRef = useRef<any>(null);
	const ramoColor = Color(ramo.color ?? getRamoColor(ramo.sigla));

	const handleMenuClose = () => {
		setBloqueMenuAnchor(null);
	};
	const handleUnfocus = () => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", false));

	const changeParalelo = (paralelo: string) =>
		props.setSelectedRamos(curr => {
			curr[curr.indexOf(ramo)] = ramosParalelos.find((r: Ramo) => r.paralelo === paralelo)!;
			return [...curr];
		});

	return (
		<>
			<Button
				ref={ramoRef}
				variant="contained" //
				style={{
					padding: "5px 20px",
					backgroundColor: ramoColor.darken(ramo.hidden ? 0.5 : 0).hex(),
					color: ramoColor.isDark() ? "#fff" : "#000",
				}}
				onClick={e => setBloqueMenuAnchor(e.currentTarget)}
				onMouseEnter={ev => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", true))}
				onMouseLeave={handleUnfocus}
				onBlur={handleUnfocus}
				endIcon={<MoreVert />}
			>
				{ramo.sigla}
			</Button>
			<Menu
				anchorEl={bloqueMenuAnchor}
				open={Boolean(bloqueMenuAnchor)} //
				onClose={handleMenuClose}
				keepMounted
			>
				<Box display="flex" flexDirection="column" margin={2} gap={1}>
					<Box margin={1} display="flex" flexDirection="column" gap={1}>
						<Typography variant="body1">
							<b>{ramo.nombre}</b>{" "}
							<span style={{ opacity: 0.6 }}>
								<i>({ramo.sigla})</i>
							</span>
							<br />
							<span style={{ opacity: 0.4 }}>DEPTO. DE {ramo.departamento}</span>
						</Typography>
						<Divider />
						<Chip icon={<AccountCircle />} label={ramo.profesor} />
						<Chip icon={<People />} label={`${ramo.cupo} POR PARALELO`} />
					</Box>
					<FormControl
						fullWidth
						style={{
							minWidth: "15vw",
						}}
					>
						<InputLabel>Paralelo</InputLabel>
						<Select
							fullWidth
							size="small"
							value={ramo.paralelo}
							label="Paralelo" //
							onChange={ev => changeParalelo(ev.target.value)}
						>
							{ramosParalelos
								.sort((a: Ramo, b: Ramo) => +a.paralelo - +b.paralelo)
								.map((r: Ramo) => (
									<MenuItem value={r.paralelo}>{r.paralelo}</MenuItem>
								))}
						</Select>
					</FormControl>
					<Box position="relative" width="100%">
						<Box display="flex" flexDirection="row" alignItems="center" gap={1} style={{ float: "left" }} width="50%">
							{!ramo.color ? (
								<></>
							) : (
								<IconButton style={{ height: "auto" }} onClick={_ => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "color", false))}>
									<FormatColorReset />
								</IconButton>
							)}
							<MuiColorInput fullWidth format="hex" value={ramo.color ?? getRamoColor(ramo.sigla)} onChange={value => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "color", value))} />
						</Box>
						<Box style={{ float: "right" }}>
							<IconButton onClick={_ => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "hidden", !ramo.hidden))} title={`${ramo.hidden ? "Mostrar" : "Ocultar"} ${ramo.sigla}`}>
								{ramo.hidden ? <Visibility fontSize="small" /> : <VisibilityOff fontSize="small" />}
							</IconButton>
							<IconButton onClick={_ => void handleMenuClose() || (window.confirm(`Confirma para borrar "${ramo.sigla}"`) && props.setSelectedRamos(curr => curr.filter(r => r.sigla !== ramo!.sigla)))} title={`Eliminar ${ramo.sigla}`}>
								<Delete fontSize="small" />
							</IconButton>
						</Box>
					</Box>
				</Box>
			</Menu>
		</>
	);
}
