export function MinutesToHHMM(minutes: number) {
	const m = minutes % 60;
	const h = (minutes - m) / 60;
	return (h < 10 ? "0" : "") + h.toString() + ":" + (m < 10 ? "0" : "") + m.toString();
}

export function HHMMtoMinutes(hhmm: string) {
	const [h, m] = hhmm.split(":").map(Number);
	return h * 60 + m;
}
