import HORARIO_ASIGNATURAS from "static/horario_asignaturas.json";

import * as React from "react";
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead } from "@mui/material";
import { HorarioTableRow as TableRow, HorarioTableHeadRow as TableHeadRow } from "./components/HorarioTable";
import { Ramo } from "../typings/horario";
import Bloque from "./components/Bloque";
import ActionButtons from "./components/ActionButtons";
import { isMobile } from "react-device-detect";
import { DIAS } from "../constants/Globals";

import * as Time from "../utils/Time";

import "./Horario.css";
import MobileWarning from "./helper/MobileWarning";
import { SettingsContext } from "contexts/SettingsContext";
import SedeJornadaPrompt from "./components/SedeJornadaPrompt";
import { RamosContext } from "contexts/RamosContext";

export default function Horario() {
	const [selectedRamos, setSelectedRamos] = React.useState<Ramo[]>([]);
	const [settings] = React.useContext(SettingsContext);
	const [_, setRamos] = React.useContext(RamosContext);

	React.useEffect(() => {
		if (!settings) return;

		const { sede, jornada } = settings;
		if (!sede || !jornada) return;

		//@ts-ignore
		setRamos(HORARIO_ASIGNATURAS[sede][jornada]);
	}, [settings?.sede, settings?.jornada]);

	return (
		<div>
			{isMobile ? <MobileWarning /> : <></>}
			{!settings?.sede && !settings?.jornada ? <SedeJornadaPrompt /> : <></>}
			<ActionButtons setSelectedRamos={setSelectedRamos} selectedRamos={selectedRamos} />
			<TableContainer component={Paper} style={{ overflowX: "hidden" }}>
				<Table size="small">
					<TableHead>
						<TableHeadRow>
							<TableCell width="8%" align="center" key={-1}>
								Bloques
							</TableCell>
							{DIAS.map((d, i) => (
								<TableCell className="Horario-header" align="center" key={i}>
									{d}
								</TableCell>
							))}
						</TableHeadRow>
					</TableHead>
					<TableBody>
						{[...Array(20).keys()].map(i => {
							i++;
							return (
								<TableRow>
									<TableCell key={-1}>
										<div
											style={{
												display: "flex",
												justifyContent: "space-around",
											}}
										>
											<span>{i}</span>
											<span style={{ opacity: i % 2 == 0 ? 0.2 : 0.6, transform: "scale(2)" }}>{Time.MinutesToHHMM(495 + 35 * (i - 1) + 15 * Math.floor(i / 2) + (i > 8 ? 45 : 0))}</span>
										</div>
									</TableCell>
									{DIAS.map((_, dia) => {
										const bloques = selectedRamos
											.filter(r => !r.hidden)
											.map(r => r.horario.map(b => ({ ...b, ramo: r })))
											.flat()
											.filter(b => b.bloque === i && b.dia === dia);
										if (!bloques) return <TableCell key={dia}></TableCell>;

										return (
											<TableCell
												width={100 / 6 + "%"}
												className={[
													//
													bloques.length > 1 ? "Horario-tope_warning" : "",
													bloques.some(b => b.ramo.ghost) ? "ghosted" : "",
													{
														"-1": "ex_catedra",
														"0": "",
														"1": "ex_ayudantia",
													}[
														Math.max(
															-1,
															Math.min(
																1,
																bloques?.map<number>(b => (b.tipo === "Ayu" ? 1 : -1)).reduce((prev, curr) => prev + curr, 0)
															)
														).toString()
													],
												].join(" ")}
												style={{
													//
													borderLeft: "4px solid #fff6",
													position: "relative",
												}}
												key={dia}
											>
												{i === 1 ? <Box position="absolute" top="0" left="0" style={{ background: "linear-gradient(black, transparent)", overflow: "hidden" }} width="calc(100% + 4px)" height="150%" /> : <></>}
												{i === 20 ? <Box position="absolute" bottom="0" left="0" style={{ background: "linear-gradient(transparent, black)", overflow: "hidden" }} width="calc(100% + 4px)" height="250%" /> : <></>}
												<Box position="relative" display="flex" flexDirection="row" justifyContent="center" alignItems="center" gap="10px" height="30px">
													{bloques?.map((bloque, i) => (
														<Bloque //
															bloque={bloque}
															ramo={bloque.ramo}
															selectedRamos={selectedRamos}
															setSelectedRamos={setSelectedRamos}
															key={i}
														/>
													))}
												</Box>
											</TableCell>
										);
									})}
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
		</div>
	);
}
